body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(138, 136, 136, 0.746);
}

.App {
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
}

.navbar {
  padding: 10px 20px;
  text-align: center;
}

.navbar h1 {
  margin: 10px 0;
}

.book-list {
  padding: 10px;
}
.book-list ul {
  padding: 0;
  list-style-type: none;
}
.book-list li {
  border-radius: 4px;
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
}
.book-list li:hover {
  opacity: 0.7;
  text-decoration: line-through;
}
.book-list .title {
  font-weight: bold;
  font-size: 1.2em;
}
.book-list .author {
  font-size: 0.9em;
}
.empty {
  margin: 20px;
  text-align: center;
}

form {
  padding: 20px;
}
input[type="text"] {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  font-size: 0.9rem;
  border: 0;
}
input[type="submit"],
.toggle-theme {
  margin: 10px auto;
  border: 0;
  padding: 6px 20px;
  display: block;
  font-size: 0.9rem;
  cursor: pointer;
}
